  <div>
    <div>
      <div class="search_container">
        <el-form :inline="true" :model="params" class="demo-form-inline">
          <el-form-item label="采购组织" size="mini" label-width="110px">
            <el-select clearable v-model="params.company_id" placeholder="请选择" @change="companyChange">
              <el-option v-for="item in companyList" :key="item.company_id" :label="item.company_name" :value="item.company_id">
                <span :class="item.type==1 ? 'optionStyle': '' ">{{ item.company_name }}</span>
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="业务日期" size="mini" label-width="110px">
            <el-date-picker v-model="timeValue" type="daterange" range-separator="-" value-format="yyyy-MM-dd" start-placeholder="开始日期" end-placeholder="结束日期">
            </el-date-picker>
          </el-form-item>
          <el-form-item label="部门" size="mini" label-width="110px">
            <el-select clearable v-model="params.department_id" placeholder="请选择" filterable>
              <el-option :label="item.department_name" :value="item.department_id" :key="item.department_id" v-for="item in departmentList.options"></el-option>
            </el-select>
          </el-form-item>

          <el-form-item label="供应商(厂家)" size="mini" label-width="110px">
            <el-select clearable v-model="params.supplier_id" filterable maxlength="100">
              <el-option v-for="item in supplier.options" :label="item.supplier_name" :value="item.supplier_id" :key="item.supplier_id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="单据号" size="mini" label-width="110px">
            <el-input v-model="params.order_sn" maxlength="100" placeholder="请输入"></el-input>
          </el-form-item>

          <el-form-item label="返利类型" size="mini" label-width="110px">
            <el-select clearable v-model="params.rebate_type_id">
              <el-option v-for="item in rebateType.options" :label="item.rebate_name" :value="item.rebate_id" :key="item.rebate_id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="返利周期" size="mini" label-width="110px">
            <el-select clearable v-model="params.rebate_cycle">
              <el-option v-for="item in rebatecycle.options" :label="item.cycle_name" :value="item.cycle_id" :key="item.cycle_id">
              </el-option>
            </el-select>
          </el-form-item>
          <el-form-item label="品牌" size="mini" label-width="110px">
            <getBrand v-model='params.brand_id' />
          </el-form-item>
          <!--<el-form-item label="备注"  size="mini" label-width="110px">-->
          <!--<el-input v-model="params.remark"></el-input>-->
          <!--</el-form-item>-->
          <el-form-item label="审核状态" size="mini" label-width="110px">
            <el-select clearable v-model="params.auditor_status">
              <el-option label="全部" :value="0">全部</el-option>
              <el-option label="未审核" :value="1">未审核</el-option>
              <el-option label="已审核" :value="2">已审核</el-option>
              <el-option label="作废" :value="3">作废</el-option>
            </el-select>
          </el-form-item>
          <el-form-item size="mini" class="search-style search">
            <el-button @click="onSearch" size="mini" type="primary">查询</el-button>
            <el-button plain @click="onReset" size="mini">重置</el-button>
          </el-form-item>
        </el-form>
      </div>
      <div class="table_container">
        <el-table :highlight-current-row="true" tooltip-effect="light" border :data="TableData.tableData" @selection-change="handleSelectionChange" max-height="600" style="width: 100%">
          <el-table-column :show-overflow-tooltip="true" type="selection" fixed="left" align="center" width="44" @selection-change="handleSelectionChange">
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="registe_time" width="56" label="操作" fixed="right">
            <template slot-scope="scope">
              <el-button @click="handleClick(scope.row)" type="text" size="small" v-if="authority.b_info==1">查看</el-button>
            </template>
          </el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="company_name" label="采购组织" width="200"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="rebate_sn" label="单据号" width="200"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="supplier_name" label="供应商(厂家)" width="200"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="brand_name" label="品牌" width="120"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="total_price" label="返利金额" width="120" align="right"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="rebate_type_name" label="返利类型" width="200"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="rebate_cycle_name" label="返利周期" width="200"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="business_time" label="业务日期" width="120"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="auditor_status_name" label="审核状态" width="100"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="auditor_name" label="审核人" width="120"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="add_user_name" label="创建人" width="120"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="add_time" label="单据时间" width="200"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="department_name" label="所属部门" width="100"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="add_user_name" label="修改人" width="200"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="update_time" label="修改时间" width="200"></el-table-column>
          <el-table-column :show-overflow-tooltip="true" property="remark" label="备注" width="200"></el-table-column>
        </el-table>
      </div>
      <div class="page_container">
        <div class="pageNumber">每页显示</div>
        <el-pagination class="erp-pagination" @size-change="handleSizeChange" :current-page.sync="goPage" :page-size="pageSize1" @current-change="handleCurrentChange" :page-sizes="per_page" layout="total,sizes, prev, pager, next,jumper" prevText="上一页" nextText="下一页" :total="TableData.total">
        </el-pagination>
      </div>
    </div>

  </div>
</template>

<script>
import {
  getData,
  getCompanyListCommon,
  getDepartmentListCommon,
} from "common/utils/public_function/commonUtils";
import { mapActions, mapGetters } from "vuex";
export default {
  name: "purchaseRebatefindDefault",
  data() {
    return {
      authority: {},
      loading: false,
      params: {},
      paramsParams: {},
      departmentList: {
        options: [],
      },
      companyList: [],
      supplier: { options: [] },
      rebatecycle: { options: [] }, //返利类型
      rebateType: { options: [] }, //返利周期
      timeValue: [],
      TableData: {
        tableData: [{}],
        total: 10,
      },
      per_page: [30, 50, 100, 500],
      pageSize: 30,
      multipleSelection: [],
      pageSize: 30,
      currentPage: 1,
      searchRrebateInfo: {}, //厂家返利查询信息
      goPage: 1, //默认搜索页面
    };
  },
  methods: {
    ...mapActions(["purchaseSearch_show"]),
    companyChange(val) {
      getDepartmentListCommon(this, this.params, 1);
    },
    onSearch() {
      if (this.timeValue) {
        this.params.business_time_start = this.timeValue[0];
        this.params.business_time_end = this.timeValue[0];
      } else {
        this.params.business_time_start = this.params.business_time_end = "";
      }
      Object.assign(this.paramsParams.params, this.params, {
        page: 1,
      });
      getData(this.paramsParams);
    },
    onReset() {
      this.$set(this, "params", {});
      this.timeValue = [];
      this.$set(this, "paramsParams", {
        params: {},
        TableData: this.TableData, //列表数据
        API: "getVenderRebateList", //接口名
        vm: this, //传递的this
      });
      getData(this.paramsParams);
    },
    handleClick(row) {
      //查看
      this.purchaseSearch_show({
        commont: "rebatemanageSearch",
        rebate_id: row.rebate_id,
        checkPage: "rebatefindPage",
      });
    },
    handleSelectionChange(val) {
      this.multipleSelection = val;
    },
    handleSizeChange(val) {
      Object.assign(this.paramsParams.params, {
        per_page: val,
        page: 1,
      });
      getData(this.paramsParams);
    },
    handleCurrentChange(val) {
      Object.assign(this.paramsParams.params, {
        page: val,
      });
      getData(this.paramsParams);
    },
  },
  mounted() {
    this.authority = JSON.parse(localStorage.getItem("authority"));
    this.$set(this, "paramsParams", {
      params: {}, //1默认带的参数
      TableData: this.TableData, //列表数据
      API: "getVenderRebateList", //接口名
      vm: this, //传递的this
    });
    getData(this.paramsParams);
    getCompanyListCommon(this); //获取组织
    this.$common.options("getSupplierListOption", this.supplier);
    this.$common.options("getRebateTypeListOption", this.rebateType); //返利类型
    this.$common.options("getRebateListOption", this.rebatecycle); //返利周期
  },
};
</script>

