<template>
  <div>
    <component v-bind:is="commont"></component>
  </div>
</template>

<script>
import rebatefindDefault from "./rebatefind_default"; //默认
import rebatemanageSearch from "../rebatemanage/searchRebatemanage";
import { mapActions, mapGetters } from "vuex";
export default {
  data() {
    return { authority: {}, commont: "rebatefindDefault" };
  },
  components: {
    rebatefindDefault, //初始化
    rebatemanageSearch //查看
  },
  computed: {
    ...mapGetters(["rebatefindDefault_showGet", "purchaseSearch_showGet"])
  },
  watch: {
    rebatefindDefault_showGet() {
      this.commont = this.rebatefindDefault_showGet.commont;
    },
    purchaseSearch_showGet() {
      this.commont = this.purchaseSearch_showGet.commont;
    }
  }
};
</script>

